/*reset*/
* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Roboto', sans-serif;
}

.mainWrapper {
    padding: 1em;
}

input {
    border: none;
    font-family: inherit;
    color: inherit;
    font-size: inherit;
    background: none;
}

:root {
    --currency-width: 3em;
}

.hide {
    display: none !important;
}

.sectionTitle {
    margin: 2em 0 1em;
}

.sectionSubtitle {
    font-size: 110%;
    margin: 1em 0 0.5em;
}

.cardTitle {
    font-size: 150%;
}

.timerBox {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.timer {
    padding: 1em;
}

.row {
    margin-bottom: 1em;
}

.titleBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.titleBar input {
    width: calc(100% - 8rem);
}

.timeEntry-details {
    padding: 1em;
}

.chipWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.chip {
    margin-right: 0.5em !important;
}

.hourlyRate {
    max-width: 8rem;
}

.card {
    margin: 0 0 1em 0;
}

.cardClientName {
    margin-top: 1em;
}

.idWrapper {
    display: inline-block;
    border-radius: 1em;
    background-color: grey;
    color: white;
    margin-right: 0.5em;
    font-size: 50%;
    padding: 0;
    width: 5em;
    height: 2em;
    line-height: 2em;
    text-align: center;
}

.backToWrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.editableText {
    -ms-flex: 1 1;
        flex: 1 1;
}

.editableTextArea {
    width: 100%;
    max-width: 70em;
    border: none;
}

.billCardHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.appbar h1 {
    cursor: pointer;
}

.optionsWrapper {
    padding: 1em;
}

.currencyField {
    width: var(--currency-width);
}

.hourlyRateField {
    width: calc(100% - var(--currency-width));
}

.loginForm {
    max-width: 40em;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 2em;
    margin-top: 41vh;
    -webkit-transform: translateY(-49%);
        -ms-transform: translateY(-49%);
            transform: translateY(-49%);
}

.loginForm > *:not(:last-child) {
    margin-bottom: 1em;
}

.deleteConfirmationMessage {
    margin-bottom: 1em;
}

.fab {
    display: none;
    position: fixed;
    right: 2em;
    bottom: 2em;
    z-index: 1;
}

.billTextTemplate {
    border: solid 1px lightgray;
}

.jsonPanelTextArea {
    width: 99%;
    height: 5em;
}

.importJsonArea {
    height: 0;
    -webkit-transition: height 0.5s;
    -o-transition: height 0.5s;
    transition: height 0.5s;
    overflow: hidden;
}

.importJsonPanel.is-open .importJsonArea {
    height: 8em;
} 

@media screen and (min-width: 30em) {
    .cardsWrapper {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        padding: 1em;
    }
    
    .card {
        margin: 0 1em 1em 0;
    }
}

@media print {
    @page 
    {
        size: auto;   /* auto is the initial value */
        margin: 0mm;  /* this affects the margin in the printer settings */
    }

    .hideInPrint,
    .hideInPrint * {
        display: none !important;
    }

    .billText {
        border: none;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
        margin: 5cm 3cm 4cm;
        font-size: 14px;
        height: calc(100vh - 9cm) !important;
    }
}




